.header__row {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header__logo {
    padding-left: 10px;
    display: flex;
    align-items: center;
}
.header__logoImg {
    padding: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 50px;
    font-weight: 700;
    border: 1px solid black;
    background-color: white;
    border-radius: 15px;
    color: black;
    box-shadow: 3px 3px 3px black;

    animation-name: logoImg;
    animation-duration: 3s;
}
@keyframes logoImg {
    from {
        transform: translate(-100px, 0px);
    }
    to {
        transform: translate(0px, 0px);
    }
}

.header__logoImg:hover {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
}
.header__logoImg:active {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 0px black;
    transform: translate(3px, 3px);
}

.header__logoText {
    padding-left: 5px;
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-text-stroke: 2px #000;
    color: white;

    animation-name: logoTxt;
    animation-duration: 3s;
}
@keyframes logoTxt {
    from {
        letter-spacing: 500px;
    }
    to {
        letter-spacing: 1px;
    }
}
@media (max-width: 320px) {
    .header__logoText {
        display: none;
    }
}

.header__menu {
    display: flex;
}
.header__menu li {
    width: 180px;
    border: 1px solid black;
    background-color: white;
    box-shadow: 3px 3px 3px black;
    text-align: center;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 10px;
    border-radius: 15px;
    padding: 10px;
    text-transform: uppercase;
    font-weight: 700;
    color: black;
}
.header__menu li:hover {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
}
.header__menu li:active {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 0px black;
    transform: translate(3px, 3px);
}


